'use client';

import Image from "next/image";
import react, { useState, useRef } from "react";
import Cam from "@/components/camera-comps";
import { GridPattern } from "@/components/ui/animated-grid-pattern";

export default function Home() {
  const [isCameraOpen, setIsCameraOpen] = useState(false);

  return (
    <div className="relative bg-purple-500 min-h-screen flex items-center justify-center">
      <GridPattern className="absolute inset-0 z-0 w-full h-full" />
      <div className="relative z-10 p-5 text-center bg-black flex flex-col items-center">
        <h1 className="text-4xl font-bold text-white mb-4">Welcome to Cardly-ai</h1>
        <p className="text-white mb-4">
          Take a picture of your card and we'll give you up to date information on its value.
        </p>
        <div
          className={`transition-all duration-500 overflow-hidden w-full ${
            isCameraOpen ? "max-h-96" : "max-h-0"
          }`}
        >
          {isCameraOpen && <Cam />}
        </div>
        <button
          onClick={() => setIsCameraOpen(!isCameraOpen)}
          className="mt-4 px-4 py-2 bg-purple-700 text-white rounded"
        >
          {isCameraOpen ? "Close Camera" : "Open Camera"}
        </button>
      </div>
    </div>
  );
}
